@use "mixins/breakpoint" as *;
@use "mixins/checkbox" as *;
@use "mixins/font-face" as *;
@use "mixins/flex" as *;
@use "mixins/disable-mob-hover" as *;
@use "mixins/shadows" as *;
@use "fonts" as *;
.btn-primary {
	padding: 0 40px;
	text-align: center;
	font-family: "GothamPro";
	font-size: 16px;
	font-weight: 700;
	line-height: 52px;
	text-transform: uppercase;
	color: var(--white);
	background: var(--primary);
	border-radius: 4px;
	transition: background 0.3s ease-in;

	&--md {
		line-height: 44px;
	}

	&:hover {
		background: var(--hover);
	}

	&:active {
		background: var(--active);
	}
}

.btn-stroke {
	padding: 0 40px;
	text-align: center;
	font-family: "GothamPro";
	font-size: 16px;
	font-weight: 700;
	line-height: 50px;
	text-transform: uppercase;
	color: var(--primary);
	background: transparent;
	border: 1px solid var(--primary);
	border-radius: 4px;
	transition: color 0.3s ease-in, background 0.3s ease-in, border 0.3s ease-in;

	&--white {
		color: var(--white);
		border-color: var(--white);
	}

	&--md {
		line-height: 44px;
	}

	&--sm {
		padding: 0 24px;
		line-height: 36px;
	}

	&:hover {
		color: var(--white);
		background: var(--primary);
		border-color: var(--primary);
	}

	&:active {
		color: var(--white);
		background: var(--active);
		border-color: var(--active);
	}
}


