@use "mixins/breakpoint" as *;
@use "mixins/checkbox" as *;
@use "mixins/font-face" as *;
@use "mixins/flex" as *;
@use "mixins/disable-mob-hover" as *;
@use "mixins/shadows" as *;
@use "fonts" as *;
.breadcrumbs {
	@include flex-v-center;

	&__item {
		position: relative;
		&:not(:last-child) {
			margin-right: 16px;

			&::after {
				content: "/";
				position: absolute;
				top: 2px;
				right: -13px;
				color: var(--gray-600);
			}
		}
	}

	&__link {
		color: var(--gray-800);
		font-size: 14px;
		font-weight: 400;
		line-height: 150%;
		transition: color 0.2s ease;

		&:hover {
			color: var(--hover);
		}

		&:active {
			color: var(--active);
		}

		&:not([href]) {
			color: var(--gray-600);
		}
	}
}
